



















































































































import { Component, Vue } from 'vue-property-decorator';

const fakeY = [7, 5, 4, 2, 4, 7, 5, 6, 5, 9];

@Component({})
export default class Dashboard extends Vue {
  private webSite: Array<{ x: number; y: number }> = [
    // { x: 100, y: 100 },
  ];

  private salesData: Array<{ x: any; y: any }> = [
    // { x: 100, y: 100 },
  ];

  private mounted() {
    for (let i = 0; i < fakeY.length; i += 1) {
      this.webSite.push({
        x: i,
        y: fakeY[i],
      });
    }

    for (let i = 0; i < 12; i += 1) {
      this.salesData.push({
        x: `${i + 1}月`,
        y: Math.floor(Math.random() * 1000) + 200,
      });
    }
  }
}
